import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './styles.scss';

class Stories extends Component{
  constructor(props) {
    super(props);
    this.setActiveStory = this.setActiveStory.bind(this);
    this.clearActiveStory = this.clearActiveStory.bind(this);
    
    this.state = {
      activeStory: '',
      prevStory: '',
      nextStory: ''
    }
  }

  componentDidMount(){
    this.setState({
      prevStory: this.props.stories[1],
      nextStory: this.props.stories[3]
    });
  }

  setActiveStory(index){
    this.setState({
      activeStory: this.props.stories[index]
    });
  }

  clearActiveStory(){
    this.setState({
      activeStory: ''
    });
  }

  render(){
    const stories = this.props.stories;
    const profileName = this.props.profileName;
    const activeStory = this.state.activeStory;
  
    return (
      <div className="collection-list-wrap">
        <h3 className="title">Stories of {profileName}</h3>
        <a className="btn btn-rt-2 btn-search"></a>
        <a className="btn btn-rt-1 btn-story"><span className="tx">Write Story</span></a>
        
        {!this.state.activeStory
        ?(
            <div>
            <div className="viewport collection-view">
              {stories.map((item, i) => {
                return (
                  <div key={i} className="collection-item-card-wrap">
                    <div className="collection-item-card">
                      <h5>{item.title}</h5>
                      <div className="desc">{item.story}</div>
                      <div className="more"
                        onClick={() => { this.setActiveStory(i) }}
                      ><a>Read more</a></div>
                    </div>
                    <div className="caption">Story by <strong>{item.author}</strong></div>
                    <div className="date">{item.date}</div>
                  </div>
                )
              })
              }
            </div>

            <div className="action"><a className="btn btn-more">Load More Stories</a></div>
            </div>
        )
        :(
          <div className="viewport fullstory-view">
            <div className="fullstory-slide">
              <div className="fullstory-card">
                  <div className="postedby">Story by <strong>{activeStory.author}</strong></div>
                  <div className="dateval">{activeStory.date}</div>
                  <a className="close"
                    onClick={this.clearActiveStory}
                  ></a>
                  <a className="flag"></a>

                  <article className="article">
                    <h3>{activeStory.title}</h3>
                    <div className="fullstory" 
                      dangerouslySetInnerHTML={{ __html: this.state.activeStory.fullStory }}
                    ></div>

                    <footer className="article-footer">
                      <div className="col col-meta">
                        <div className="meta-gr">
                          <h6>Reads</h6>
                          <div className="meta-val reads">
                            {activeStory.reads}
                          </div>
                        </div>
                        <div className="meta-gr">
                          <h6>Reads</h6>
                          <div className="meta-val salutes">
                            {activeStory.salutes}
                          </div>
                        </div>
                        <div className="meta-gr">
                          <h6>Shares</h6>
                          <div className="meta-val shares">
                            {activeStory.shares}
                          </div>
                        </div>
                      </div>
                      <div className="col">
                        <a className="btn btn-salute">Salute</a>
                        <a className="btn btn-share">Share</a>
                      </div>
                    </footer>

                    <a className="slide-arrow prev"></a>
                    <a className="slide-arrow next"></a>
                  </article>
              </div>
            </div>
            <div className="fullstory-navs">
              <div className="col">
                <h5><a>Previous Story</a></h5>
                <h4><a>{this.state.prevStory.title}</a></h4>
              </div>
              <div className="col">
                  <h5><a>Next Story</a></h5>
                <h4><a>{this.state.nextStory.title}</a></h4>
              </div>
            </div>
          </div>
        )
        }
        
      </div>
    )
  }
}

Stories.propTypes = {
  prop: PropTypes.object
}

export default Stories;



